body,
html {
  position: relative;
  height: 100%;
  width: 100%; }

body,
h1,
h2,
h3,
h4,
h5,
html,
p {
  margin: 0;
  padding: 0; }

*,
:after,
:before {
  box-sizing: border-box; }

ul {
  list-style-type: none;
  margin: 0;
  padding: 0; }

.clearfix:after {
  content: "";
  display: table;
  clear: both; }

@font-face {
  font-family: "Times New Romance";
  font-display: swap;
  src: url("/fonts/TimesNewRomanPSMT.eot?") format("eot"), url("/fonts/TimesNewRomanPSMT.woff") format("woff"), url("/fonts/TimesNewRomanPSMT.ttf") format("truetype"); }

.title {
  position: relative;
  font-family: "Open Sans", sans-serif;
  font-size: 1.875em;
  line-height: 1.4em;
  letter-spacing: 3px;
  padding-top: 33px;
  margin-bottom: 70px;
  text-transform: uppercase;
  color: #2c2e37;
  text-align: center; }
  .title::before {
    content: '';
    position: absolute;
    top: 0;
    left: 50%;
    transform: translateX(-50%);
    width: 80px;
    height: 7px;
    background: url("../img/title-border.png") center no-repeat; }

.journal {
  padding-top: 106px; }
  .journal__posts {
    display: grid;
    grid-template-columns: repeat(3, 3fr);
    grid-auto-rows: minmax(380px, auto);
    box-shadow: 0 0 21px rgba(6, 6, 6, 0.13); }
  .journal__post {
    position: relative; }
    .journal__post--love::before, .journal__post--tree::before, .journal__post--water::before {
      content: '';
      position: absolute;
      width: 0;
      height: 0;
      z-index: 100; }
    .journal__post--love::before {
      top: 50px;
      left: -10px;
      border-top: 10px solid transparent;
      border-right: 10px solid #fff;
      border-bottom: 10px solid transparent; }
    .journal__post--tree::before {
      top: 50px;
      right: -10px;
      border-top: 10px solid transparent;
      border-left: 10px solid #fff;
      border-bottom: 10px solid transparent; }
    .journal__post--water::before {
      top: -10px;
      left: 53px;
      border-left: 10px solid transparent;
      border-right: 10px solid transparent;
      border-bottom: 10px solid #fff; }
  .journal__image__bg {
    width: 100%;
    height: 100%; }
  .journal__icon {
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%); }
  .journal__content {
    display: flex;
    flex-direction: column;
    /* justify-content: space-between;
        align-items: center; */
    height: 100%;
    padding: 41px 30px;
    text-align: center; }
    .journal__content__label {
      font-family: "Open Sans", sans-serif;
      font-size: 0.6875em;
      font-weight: 600;
      text-transform: uppercase;
      letter-spacing: 1px;
      color: #16b5ef;
      margin-bottom: 47px; }
    .journal__content__title {
      font-family: "Open Sans", sans-serif;
      font-size: 1em;
      font-weight: 600;
      text-transform: uppercase;
      letter-spacing: 2.4px;
      color: #2c2e37;
      margin-bottom: 19px; }
      .journal__content__title > span {
        position: relative;
        display: inline-block; }
        .journal__content__title > span::before {
          content: '';
          position: absolute;
          top: 50%;
          left: 0;
          height: 2px;
          width: 100%;
          transform: translateY(-50%);
          background-color: #151517; }
    .journal__content__text {
      font-family: "Open Sans", sans-serif;
      font-size: 0.875em;
      line-height: 1.5625em;
      font-weight: 400;
      color: #777777; }
    .journal__content__page {
      display: flex;
      justify-content: center;
      align-items: flex-end;
      flex-grow: 1;
      font-family: "Times New Romance", sans-serif;
      font-size: 0.875em;
      font-weight: 400;
      font-style: italic;
      color: #2c2e37; }

.work {
  margin-top: 113px; }
  .work .title {
    margin-bottom: 64px; }
  .work__bg {
    display: flex;
    justify-content: center; }
  .work__list {
    display: flex;
    justify-content: space-between;
    padding: 0 40px; }
  .work__item {
    width: 190px;
    text-align: center;
    font-family: "Roboto", sans-serif; }
  .work__title {
    margin-top: 24px;
    color: #2c2e37;
    font-size: 1.125em;
    font-weight: 900; }
  .work__text {
    font-size: 1em;
    color: #949494;
    margin: 0 -10px; }

.travel {
  position: relative;
  background: url("../img/travel-bg.png") center bottom no-repeat;
  margin-top: 97px;
  padding-bottom: 475px; }
  .travel .title {
    margin-bottom: 89px; }
  .travel__cards {
    display: flex;
    justify-content: space-between; }
  .travel__card {
    position: relative;
    width: 200px;
    height: 382px;
    padding: 4px 12px 20px 20px;
    border-radius: 3px;
    font-family: "Open Sans", sans-serif;
    color: #fff; }
    .travel__card__img {
      position: absolute;
      top: 0;
      left: 0;
      width: 100%;
      height: 100%; }
    .travel__card__content {
      position: relative;
      display: flex;
      flex-direction: column;
      justify-content: space-between;
      height: 100%; }
  .travel__title {
    font-size: 1.75em;
    font-weight: 400;
    margin-top: 15px;
    margin-bottom: 7px; }
  .travel__text {
    font-size: 1em; }
  .travel__prices {
    display: flex;
    justify-content: space-between;
    align-items: center; }
  .travel__price {
    font-size: 1.625em;
    line-height: 0.9em;
    font-weight: 800; }
    .travel__price--old {
      font-size: 1.125em;
      font-weight: 400; }

.gallery {
  margin: 73px 0 80px; }
  .gallery .title {
    margin-bottom: 63px; }
  .gallery__images {
    display: grid;
    grid-template-columns: repeat(3, 1fr);
    grid-gap: 30px;
    grid-auto-flow: dense; }
  .gallery__row:not(:last-child) {
    margin-bottom: 30px; }

.partenrs {
  background-color: #f8f9fa;
  padding: 74px 0 71px; }
  .partenrs__list {
    display: flex;
    justify-content: space-between;
    align-items: center; }
  .partenrs__item img {
    filter: brightness(0); }

.reviews {
  padding-top: 70px;
  background: url("../img/reviews-bg.png") center bottom no-repeat; }
  .reviews .title {
    margin-bottom: 82px; }
  .reviews .review {
    padding: 0 10px;
    font-family: "Open Sans", sans-serif; }
    .reviews .review__quotes {
      margin-bottom: 12px; }
    .reviews .review__text {
      margin-bottom: 24px;
      font-size: 1.125em;
      line-height: 1.8em;
      color: #bcbcbc; }
  .reviews .client {
    display: flex;
    align-items: center; }
    .reviews .client__profile {
      margin-right: 20px; }
    .reviews .client__name {
      font-size: 1em;
      font-weight: 600;
      color: #16b5ef; }
    .reviews .client__position {
      font-weight: 400; }

.contacts {
  display: flex;
  justify-content: center;
  margin: 140px 0 163px; }

.form {
  width: 514px;
  padding: 36px 28px 50px;
  margin-bottom: 0;
  border-radius: 3px;
  font-family: "Open Sans", sans-serif;
  text-align: center;
  background: transparent url("../img/form-bg.png") center no-repeat; }
  .form__title {
    margin-bottom: 7px;
    font-size: 2.25em;
    font-weight: 600;
    line-height: 1em;
    letter-spacing: 4px;
    color: #16b5ef; }
  .form__text {
    font-size: 1em;
    color: #383e4a;
    margin-bottom: 32px; }
  .form__input {
    display: block;
    width: 100%;
    height: 60px;
    margin-bottom: 20px;
    padding: 22px 20px;
    border-radius: 3px;
    font-family: "Roboto", sans-serif;
    font-size: 1.125em;
    font-weight: 400;
    background-color: #fff;
    color: #ccc;
    border: none;
    outline: none; }
    .form__input::placeholder {
      color: #ccc; }
    .form__input:-ms-input-placeholder {
      color: #ccc; }
    .form__input::-ms-input-placeholder {
      color: #ccc; }
  .form__submit {
    display: block;
    width: 100%;
    height: 70px;
    border-radius: 3px;
    border: none;
    outline: none;
    background-color: #16b5ef;
    font-family: "Open Sans", sans-serif;
    font-size: 1em;
    font-weight: 600;
    text-transform: uppercase;
    letter-spacing: 2px;
    color: #fff;
    transition: background-color ease .2s; }
    .form__submit:hover {
      background-color: #12a4d9; }

.footer {
  display: flex;
  justify-content: space-between;
  padding: 23px 0; }
  .footer .container {
    padding: 0 20px; }
  .footer__authority {
    font-family: "Open Sans", sans-serif;
    font-size: 1.125em;
    color: #6E9AA8; }
  .footer__phone {
    display: inline-block;
    float: right;
    font-family: "Roboto", sans-serif;
    font-size: 1.6875em;
    font-weight: 900;
    color: #6E9AA8;
    text-decoration: none;
    transition: color .2s ease; }
    .footer__phone:hover {
      text-decoration: none; }

.socials {
  display: flex;
  justify-content: center; }
  .socials__item:not(:last-child) {
    margin-right: 5px; }
  .socials__link {
    display: inline-block; }
